import React from 'react'
import { NavLink } from 'react-router-dom'
import { AiOutlineMenu, AiOutlineClose } from 'react-icons/ai';
import "./Navbar.scss"
import Image1 from "../assets/door.png"
import Image2 from "../assets/book.png"
import Image3 from "../assets/vocab.png"
import Image4 from "../assets/translate.png"
import Image5 from "../assets/doorClosed.png"


const Navbar = () => {
const [nav, setNav] = React.useState(false)

  return (
    <div>
        <div className="navbar">
        <div className="container nav-container">
                      

 <div className={"box"}>
          <div
            className={`${nav === true ? ["menu-items", "active"].join(' ') : ["menu-items"]} `}
          >
            <li><a href="#"><img style={{width: "40px"}} src={Image1} alt="" /> Начать</a></li>
            <li><NavLink onClick={() => setNav(false)} to={"/my-books"}><img style={{width: "40px", marginTop: "-10px"}} src={Image2} alt="" /> Мои книги</NavLink></li>
            <li><a href="#"><img style={{width: "40px"}} src={Image3} alt="" /> Cловарь</a></li>
            <li><a href="#"><img style={{width: "40px"}} src={Image4} alt="" /> Переводчик</a></li>
            {/* <li><NavLink onClick={() => setNav(false)} to={"/contact"}>Контакты</NavLink></li> */}
            <li><a href="#"><img style={{width: "40px"}} src={Image5} alt="" /> Личный Кабинет</a></li>
          </div>
          <div onClick={() => setNav(!nav)} className={"mobile_btn"}>
            {nav ? <AiOutlineClose size={35} /> : <AiOutlineMenu size={35} />}
          </div>
        </div>
        </div>
      </div>
       
    </div>
  )
}

export default Navbar