import { useState } from "react";
import "./Translate.scss"
import { AiOutlineClose } from 'react-icons/ai';
// @ts-ignore
import Rodal from 'rodal';
import 'rodal/lib/rodal.css';

interface Props {
    inputText: any,
    setChosen: any,
    show: any,
    setShow: any,
    chosen: any,
    highLight: any
}

const Translate = ({inputText, setChosen, show, setShow, chosen, highLight}:Props): JSX.Element => {
  const [inputText2, setInputText2] = useState<string>('');
  const [outputLang, setOutputLang] = useState<string>('ar');
  const [outputText, setOutputText] = useState<string>('');
  const [isTranslated, setIsTranslated] = useState<boolean>();

  const translate = () => {
    console.log(outputLang);
    const options = {
      method: 'POST',
      headers: {
        'content-type': 'application/json',
        'X-RapidAPI-Key': '41a6c0f2d0mshe52f060738b60dep165ba8jsne45b13573230',
        'X-RapidAPI-Host': 'microsoft-translator-text.p.rapidapi.com'
      },
      body: `[{"Text":"${inputText || inputText2}"}]`
    };
    const url = 'https://microsoft-translator-text.p.rapidapi.com/translate?to%5B0%5D=ru&api-version=3.0&profanityAction=NoAction&textType=plain';

    fetch(url, options)
      .then(response => {
        if (response.status !== 200) {
          setIsTranslated(false);
          console.log("there's an error");
          return;
        }
        setIsTranslated(true);
        response.json()
          .then(response => {
            const translatedText = response[0].translations[0].text;
            setOutputText(translatedText);
            console.log(translatedText);
          })
      })
      .catch(err => {
        setIsTranslated(false);
        console.error(err)
      });
  }

  const clearInput = () => {
    highLight("")
    setOutputText("");
    setIsTranslated(false);
    setChosen(false)
    setInputText2("")
  }

  function close2() {
    setShow(!show)
    setChosen(false)
    setOutputText("");
    highLight("")
    setInputText2("")
  }
  return (
    <Rodal customStyles={{overflowY: "auto"}} className={""} animation={"slideDown"} visible={show} onClose={close2}>
 <section className="translator">
        
        <div className="row-wrapper">
          <div className="translator-container input-lang">
            
            <form className="input-form">
              <textarea
                className="text-box"
                placeholder="Enter text (any language)"
                onChange={e => setInputText2(e.target.value)}
                value={inputText || inputText2}
              >
              </textarea>
              <div className="top-row">
              <button
                className="btn btn-primary btn-translate w-100 p-2"
                onClick={translate}
              >
                Translate
              </button>
            </div>
              {
                inputText !== "" &&
                <AiOutlineClose
                  className="icon-btn close-btn"
                  onClick={clearInput}
                />
              }
            </form>
          </div>
          <div className="translator-container output-lang">
            {/* <div className="top-row">
              <select
                name="languages"
                id="languages"
                className="form-select form-select-sm"
                onChange={e => setOutputLang(e.target.value)}
              >
                <option value="ar">Arabic</option>
                <option value="en">English</option>
                <option value="fr">French</option>
                <option value="de">German</option>
                <option value="es">Spanish</option>
                <option value="kor">Korean</option>
              </select>
            </div> */}
            <p className="text-box output-box">
              {
                isTranslated === false ?
                  <span className="output-placeholder translation-error">Translation failed</span>
                  :
                  outputText === "" ?
                    <span className="output-placeholder">Empty</span>
                    :
                    outputText
              }
            </p>
          </div>
        </div>
      </section>         

        </Rodal>
   
  )}
export default Translate
