import React, { useState, useRef, useEffect} from 'react'
import "./App.css"
import Navbar from './Navbar/Navbar';
import MenuContext from './Components/MenuContext';
import Loader from './Components/Loader/Loader';
import { Route, Routes } from 'react-router-dom';
import CustomNav from './Navbar/CustomNav';
import Image from "./assets/door.png"
import Image3 from "./assets/vocab.png"
import Image2 from "./assets/book.png"
import Image4 from "./assets/translate.png"
import Image5 from "./assets/doorClosed.png"
import MyBooks from './Components/Page2/MyBooks';


function App() {
  const [loading, setLoading] = useState<boolean>(false);  
  const [data, setData] = useState<any>([])
  useEffect(() => {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
    }, 2000);
  }, []);


    useEffect(() => {
        let newData = localStorage.getItem('data')
        setData(newData? JSON.parse(newData) : [])
    }, [])

  return (
    <div className="container">

      <div className='div1'> 
      <CustomNav
      li={[
        ["Начало", Image, "/"],
        ["Мои книги", Image2, "/my-books"],
        ["Словарь", Image3],
        ["Переводчик", Image4],
        ["Личный кабинет", Image5]
      ]}
    />
          <Navbar /> 
        
      </div>
      <div className='div2'>
        {loading ? <Loader /> : <Routes>
  <Route path='/' element={<MenuContext data={data}/>} />
  <Route path='/my-books' element={<MyBooks data={data}/>} />
</Routes> }
      </div>
    </div>
  );
}

export default App;