import { configureStore, createSlice } from "@reduxjs/toolkit";
import { getFromLocalStorage, saveToLocalStorage } from "./utlis";

 const postSlice = createSlice({
  name: "books",
  initialState: {
    currentItem: "",
    basket: getFromLocalStorage("books")
  },
  reducers: {
    handleSave: ({state, action}:any) => {

      state.basket.push({ ...action.payload});
      saveToLocalStorage("books", state.basket);
    },

    delProduct: ({state, action}:any) => {
      let items = JSON.parse(localStorage.getItem("books")|| '{}');
      items = state.basket.filter((item:any) => item.id !== action.payload);
      saveToLocalStorage("books", items);
      if (items.length === 0) {
        localStorage.removeItem("books");
      } 
    }
  },

});


export const store = configureStore({
  reducer: {
      counterReducer: postSlice.reducer
  }
})