import React, { useState } from "react";
import { AiOutlineMenu, AiOutlineClose } from 'react-icons/ai';
import s from "./_CustomNav.module.scss"
import { NavLink } from "react-router-dom";


interface Props {
  li: any
}


const CustomNav = ({li}:Props) => {
  const [window, setWindow] = useState(true);


  let openClose = () => {
    if (window === true) {
      setWindow(false);
    } else {
      setWindow(true);
    }
  };
  return (
    <nav className={s.navbarMenu} style={{ width: window === false ? 300 : 80 }}>
      <div className={s.burger} onClick={() => openClose()}>
      {window ? <AiOutlineMenu size={35} /> : <AiOutlineClose size={35} />}
      </div>
      <ul className={s.navbar__list}>
        {li.map((item:any, i:any) => (
          <NavLink to={item[2]} className={s.navbar__liBox} key={i}>
            
            
              <img
              src={item[1]}  
              style={{ paddingLeft: window === false ? 27 : 25, width: "60px", paddingTop: "3px" }}
            />
            
              <li
              className={s.navbar__li}
              style={{ display: window === false ? "inline-block" : "none", fontSize: "20px" }}
            >
              {item[0]}
            </li>
          </NavLink>
        ))}
      </ul>
    </nav>
  );
};

export default CustomNav;
